/*
 * Note:
 * 1) Some Popovers require scrolling
 * 2) We don't know if content will have a scrollbar ahead of time
 * 3) We want a small margin left of a potential scrollbar
 * 4) We want a consistent large margin right of the content in case of no scrollbar
 * To achieve 3 and 4, we split our large margin and apply one part
 * to the container (space right of scrollbar) and the other part to the content
 * (space left of scrollbar (3)). If no scrollbar is present, the two spacings collapse
 * into a single big one (4).
 */
.UBQ_Popover-module__popover--TDOA- {
  background-color: var(--ubq-elevation-3);
  box-sizing: border-box;
  border: 1px solid var(--ubq-border-default);
  border-radius: var(--ubq-border_radius-l);
  overflow: hidden;
  padding: 0 calc(var(--ubq-margin-s) - calc(var(--ubq-margin-xs) / 2));
  box-shadow: var(--ubq-effect-shadow);
  z-index: 80;
}

.UBQ_Popover-module__popoverContent--h-2aO > *:first-child {
  padding-top: var(--ubq-margin-s);
}
.UBQ_Popover-module__popoverContent--h-2aO > *:last-child {
  padding-bottom: var(--ubq-margin-s);
}
.UBQ_Popover-module__popoverContent--h-2aO > *:first-child,
.UBQ_Popover-module__popoverContent--h-2aO > *:last-child {
  padding-right: calc(var(--ubq-margin-xs) / 2);
}
.UBQ_Popover-module__popoverContent--h-2aO > *:only-child {
  padding: var(--ubq-margin-s) calc(var(--ubq-margin-xs) / 2);
}