/* NOTE:
 * This is needed for a fix regarding Safari and `react-draggable`, where dragging
 * the know inside our `SaturationBrightness` component triggers text selection
 * selecting a whole bunch of stuff if the mouse draggs outside of the knobs
 * container. The class `.react-draggable-transparent-selection` is an internal
 * class the library uses to add styling while dragging an element happens to
 * remove selection styling - which, apparently, does not work in Safari.
 * At the time of writing this, the latest Safari (14.1.2) and older and
 * `react-draggable` version 4.2.0 (currently installed) and 4.4.4 (current latest)
 * have been tested positive for the issue
 * I have opened an issue on the `react-draggable` repository which can be found
 * here: https://github.com/react-grid-layout/react-draggable/issues/605
 * */
.react-draggable-transparent-selection {
  user-select: none;
}

.UBQ_GlobalStyles-module__block--zGWhl {
  height: 100%;
  width: 100%;
}

:host {
  all: initial;
}