.UBQ_NumberInput-module__input--6EI9x {
  /* NOTE: The additional `1ch` is used as a puffer. If the last character
  is only slightly overflowing the user agent will start truncating.
   Adding an additional `1ch` to the width calculating basically adds space for
   an additional character and therefore prevents truncation.
  The added `--ubq-margin-s` relates to the left margin which is being declared
