.UBQ_InputGrid-module__block--zRPAf {
  display: grid;
  gap: 1px;
  grid-template-columns: 1fr 1fr;
}
.UBQ_InputGrid-module__block--zRPAf button, .UBQ_InputGrid-module__block--zRPAf input {
  border-radius: 0;
}
.UBQ_InputGrid-module__block--zRPAf > *:first-child button, .UBQ_InputGrid-module__block--zRPAf > *:first-child input {
  border-radius: var(--ubq-border_radius-s) 0 0 0;
}
.UBQ_InputGrid-module__block--zRPAf > *:nth-child(2) button, .UBQ_InputGrid-module__block--zRPAf > *:nth-child(2) input {
  border-radius: 0 var(--ubq-border_radius-s) 0 0;
}
.UBQ_InputGrid-module__block--zRPAf > *:nth-last-child(2) button, .UBQ_InputGrid-module__block--zRPAf > *:nth-last-child(2) input {
  border-radius: 0 0 0 var(--ubq-border_radius-s);
}
.UBQ_InputGrid-module__block--zRPAf > *:last-child button, .UBQ_InputGrid-module__block--zRPAf > *:last-child input {
  border-radius: 0 0 var(--ubq-border_radius-s) 0;
}