.UBQ_SaturationBrightnessSelector-module__block--RHwQ8 {
  position: relative;
  width: 100%;
  height: 100%;
  /* NOTE: This is needed for `.gradient` to be displayed correctly*/
  background: white;
  border-radius: var(--ubq-border_radius-s);
}
.UBQ_SaturationBrightnessSelector-module__block--RHwQ8.UBQ_SaturationBrightnessSelector-module__isDisabled--HJanP {
  opacity: 0.55;
  pointer-events: none;
}

.UBQ_SaturationBrightnessSelector-module__gradient--VMHwF {
  position: relative;
  cursor: pointer;
  border-radius: var(--ubq-border_radius-s);
  outline: 1px solid var(--ubq-stroke-contrast-2);
  outline-offset: -1px;
  width: 100%;
  height: calc(30 * var(--ubq-scale-base));
  background: linear-gradient(to top, rgb(0, 0, 0), rgba(0, 0, 0, 0)), linear-gradient(to right, hsla(var(--ubq_internal-SaturationBrightnessSelector-current-hue, 0), 100%, 50%, 0), hsl(var(--ubq_internal-SaturationBrightnessSelector-current-hue, 0), 100%, 50%));
}
@supports not (contain: none) {
  .UBQ_SaturationBrightnessSelector-module__gradient--VMHwF:focus {
    outline: none;
    background-color: inherit;
    box-shadow: var(--ubq-effect-focus);
  }
}
.UBQ_SaturationBrightnessSelector-module__gradient--VMHwF:focus-visible {
  outline: none;
  background-color: inherit;
  box-shadow: var(--ubq-effect-focus);
}

.UBQ_SaturationBrightnessSelector-module__thumb--rRWCX {
  border-radius: 50%;
  border: 2px solid white;
  box-shadow: 0 0 0 2px rgba(0, 0, 0, 0.16);
  box-sizing: border-box;
  height: calc(var(--ubq-scale-base) * 3);
  width: calc(var(--ubq-scale-base) * 3);
}