.UBQ_Seeker-module__seeker--utxX8 {
  --ubq_internal-Seeker_interaction_area-size: calc(5 * var(--ubq-scale-base));
  height: 100%;
  display: grid;
  grid-template-areas: "trackerStack";
  grid-template-rows: 100%;
  grid-template-columns: 100%;
  justify-items: center;
  align-items: start;
  pointer-events: none;
}
.UBQ_Seeker-module__seeker--utxX8:focus-visible {
  box-shadow: var(--ubq-effect-focus);
}

.UBQ_Seeker-module__head--eUSLf {
  grid-area: trackerStack;
  width: calc(var(--ubq-scale-base) * 2);
  height: calc(var(--ubq-scale-base) * 3);
  /*NOTE: This padding is adding an enlarged interaction area around the seeker
   head to make it easier to be grabbed by a pointing device. The `transform`
   declaration moves the head svg visually back in place.*/
  padding-inline: var(--ubq_internal-Seeker_interaction_area-size);
  padding-top: var(--ubq_internal-Seeker_interaction_area-size);
  transform: translateY(calc(-1 * var(--ubq_internal-Seeker_interaction_area-size)));
  display: flex;
  color: var(--ubq-static-selection-frame);
  pointer-events: initial;
  cursor: grab;
}
.UBQ_Seeker-module__head--eUSLf.UBQ_Seeker-module__grabbing--YYwnH {
  cursor: grabbing;
}
.UBQ_Seeker-module__head--eUSLf svg {
  width: calc(var(--ubq-scale-base) * 2);
  height: calc(var(--ubq-scale-base) * 3);
}

.UBQ_Seeker-module__bar--H5eK0 {
  grid-area: trackerStack;
  width: 2px;
  height: 100%;
  border-radius: var(--ubq-border_radius-s);
  background-color: var(--ubq-static-selection-frame);
}