/**
 * font-family and fixed width sizing should be replaced. Used here because of
 * unexpected behaviour with text input selection having Times Roman set as font
 * and both inputs being somewhere controled in sized
 * */
.UBQ_FontInput-module__block--DCCC5 {
  display: grid;
  grid-template-columns: repeat(6, minmax(0, 1fr));
  grid-auto-rows: 24px;
  column-gap: var(--ubq-margin-s);
  row-gap: var(--ubq-margin-s);
}

.UBQ_FontInput-module__fontFamilySelect--CIZ0u,
.UBQ_FontInput-module__fontStyleSelect--8--vg {
  display: flex;
}

.UBQ_FontInput-module__fontFamilySelect--CIZ0u,
.UBQ_FontInput-module__fontStyleSelect--8--vg {
  display: flex;
}

.UBQ_FontInput-module__fontFamilySelect--CIZ0u,
.UBQ_FontInput-module__fontSizeSelect--flszW {
  grid-column-start: 1;
  grid-column-end: span 3;
}

.UBQ_FontInput-module__fontStyleSelect--8--vg,
.UBQ_FontInput-module__fontStyleToggles--XOeir {
  grid-column-start: 4;
  grid-column-end: span 3;
}

.UBQ_FontInput-module__fontStyleToggles--XOeir {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: var(--ubq-margin-s);
}