.UBQ_Separator-module__block--90X0b {
  display: block;
  flex-shrink: 0;
  background: var(--ubq-stroke-contrast-1);
  border: none;
}
.UBQ_Separator-module__block--90X0b[aria-orientation=horizontal] {
  width: 100%;
  height: 1px;
}
.UBQ_Separator-module__block--90X0b[aria-orientation=vertical] {
  width: 1px;
  height: auto;
}
:where(.UBQ_Separator-module__block--90X0b) {
  margin-block: var(--ubq-margin-xs);
}