.UBQ_AssetLoadingSpinner-module__block--sa-XM {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--ubq-foreground-default);
}

.UBQ_AssetLoadingSpinner-module__loadingSpinnerIcon--p8Z3l {
  /* NOTE: The icon is being initially hidden before the animation starts.
   This is a fix to prevent the spinner from flickering in cases where the loading
   time is a very short amount, e.g. choosing a color in one color library. The 200ms
   value is based on an educated guess and might still result in some flickering
   if the loading time is right around that amount, but will not trigger for
   nearly instant loading times, like in the above described case.
   */
  animation: UBQ_AssetLoadingSpinner-module__loading--CJ2M7 1.1s infinite linear, UBQ_AssetLoadingSpinner-module__fade-in--fMZk4 200ms 1 ease-in-out;
  animation-delay: 200ms;
  height: calc(var(--ubq-scale-base) * 12);
  width: calc(var(--ubq-scale-base) * 12);
  opacity: 0;
}

@keyframes UBQ_AssetLoadingSpinner-module__loading--CJ2M7 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes UBQ_AssetLoadingSpinner-module__fade-in--fMZk4 {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}